@import "../../global.scss";

.contact {
	background-color: white;
	display: flex;

	@include mobile {
		flex-direction: column;
	}
	.left {
		flex: 2;
		display: flex;
		align-items: center;
		img {
			width: 100%;
		}
	}
	.right {
		flex: 2;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		h2 {
			font-size: 30px;
		}

		form {
			width: 70%;
			height: 70%;
			display: flex;
			align-items: center;
			flex-direction: column;
			justify-content: space-around;

			input {
				width: 300px;
				height: 30px;
				font-size: 14px;
				border: 2px solid gray;
				border-radius: 5px;

				@include mobile {
					width: 180px;
					height: 20px;
				}
			}
			textarea {
				width: 300px;
				height: 200px;
				font-size: 14px;
				border: 2px solid gray;
				border-radius: 5px;

				@include mobile {
					width: 180px;
					height: 100px;
				}
			}

			button {
				width: 150px;
				height: 30px;
				background-color: crimson;
				border: none;
				border-radius: 5px;
				color: white;
				font-weight: 500;
			}
			span {
				color: green;
			}
		}
	}
}
